.galeria {
    font-family: Arial, Helvetica, sans-serif;
    margin-bottom: 2rem;
    margin-left: 245px;
  
    h2 {
      color: #7b78e5;
      font-size: 24px;
    }
  
    &__cards {
      list-style: none;
      padding: 0;
      display: flex;
      width: 100%;
      flex-wrap: wrap;
    }
  
    &__card {
      background-color: #041833;
      border-radius: 20px;
      height: 280px;
      width: 285px;
      margin-right: 1rem;
      margin-bottom: 1.35rem;
      cursor: pointer;
  
      &:hover {
        box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
        -moz-transform: scale(1.1);
        -webkit-transform: scale(1.1);
        transform: scale(1.025);
        transition: 0.18s;
      }
  
      div {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 1rem;
        box-sizing: border-box;
        width: 100%;
  
        p {
          margin: 0;
          margin-top: 0.5rem;
          color: #9d9d9d;
        }
  
        span {
          width: 20%;
          display: flex;
          justify-content: space-between;
          align-items: center;
  
          img {
            cursor: pointer;
          }
        }
      }
    }
  
    &__imagem {
      height: 75%;
      width: 100%;
      border-top-left-radius: 15px;
      border-top-right-radius: 15px;
    }
  
    &__descricao {
      margin: 0;
      padding: 0 16px;
      font-size: 1.25rem;
      color: #d9d9d9;
      font-family: Arial, Helvetica, sans-serif;
      text-align: center;
      margin-top: 15px;
    }
  }