.tags {
    width: 65vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #d9d9d9;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
   
    p {
      font-size: 1.25rem;
    }
   
    &__lista {
      list-style: none;
      width: 380px;
      padding: 0;
      display: flex;
     justify-content: space-between;
   
      li {
        background-color: rgba(148, 163, 184,0.3);
        padding: 6px 10px;
        border-radius: 8px;
        cursor: pointer;
        margin-right: 20px;
      }
    }
  }